@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
  #app{
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

html, body, #root, #app{
    height: 100%;
}

body{
    background: #E1E1E1;
    -webkit-font-smoothing: antialiased;
}

body, input, button{
    font-family: Roboto, sans-serif;
}
aside{
    width: 320px;
    height: 100%;
    background: #434343;
    color: #FFF;
    padding-top: 30px;
    text-shadow: 2px 2px black ;
}

aside strong{
    padding: 30px 30px;
    font-size: 34px;
    text-align: left;
    color: #FFF;
}

.menu{
    height: auto;
    margin-top: 30px;
}

.menu-option span{
    font-size: 20px;
}

.menu-option{
    padding: 10px 40px;
    border-style: solid;
    border-width: 1px 0px;
    border-color: #707070;
    display: block;
    color: #FFF;
    text-decoration: none;
}

.menu-option + .menu-option{
    border-top: 0;
}

.menu-option:hover{
    background-color: #303030;
    cursor: pointer;
}
